import React, { FC, useEffect, useState } from 'react';
import { BucketResponse, BucketsRepository } from './BucketsRepository';
import { BucketsList } from './BucketsList';

export const Buckets: FC = () => {
  const [buckets, setBuckets] = useState([] as BucketResponse[]);

  useEffect(() => {
    const result = BucketsRepository.findAll();
    setBuckets(result);
  }, []);

  return <BucketsList buckets={buckets} />;
};
