import React, { FC, useState } from 'react';
import { BucketResponse } from './BucketsRepository';
import {
  Accordion,
  AccordionActions,
  AccordionDetails,
  AccordionSummary,
  Avatar,
  Button,
  Divider,
  InputAdornment,
  makeStyles,
  TextField,
  Typography,
} from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { Money } from '../components/Money';
import { AddCircleOutline, CancelOutlined, CheckCircleOutlined, RemoveCircleOutline } from '@material-ui/icons';

interface BucketsListProps {
  buckets: BucketResponse[];
}

const useStyles = makeStyles(theme => ({
  root: {
    marginLeft: '2%',
    marginRight: '2%',
    marginTop: '2%',
    marginBottom: '4%',
  },
  card: {
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  icon: {
    marginRight: '1em',
  },
  name: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexGrow: 1,
    flexShrink: 0,
  },
  amount: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
}));

export const BucketsList: React.FC<BucketsListProps> = ({ buckets }) => {
  const classes = useStyles();
  const [expanded, setExpanded] = useState('');

  return (
    <div className={classes.root}>
      {buckets.map(bucket => (
        <Bucket
          key={bucket.id}
          bucket={bucket}
          expanded={expanded === bucket.id}
          onExpand={id => setExpanded(id)}
          onHideDetails={() => setExpanded('')}
        />
      ))}
    </div>
  );
};

interface BucketProps {
  bucket: BucketResponse;
  expanded: boolean;
  onExpand: (id: string) => void;
  onHideDetails: (id: string) => void;
}

const Bucket: FC<BucketProps> = ({ bucket: { id, name, icon, amount }, expanded, onExpand, onHideDetails }) => {
  const classes = useStyles();

  const onChange = (expand: boolean) => {
    if (expand) {
      onExpand(id);
    } else {
      onHideDetails(id);
    }
  };

  return (
    <Accordion expanded={expanded} onChange={(_, expand) => onChange(expand)}>
      <AccordionSummary expandIcon={<ExpandMoreIcon />} classes={{ content: classes.card }}>
        <Avatar className={classes.icon} alt={name} title={name}>
          {icon}
        </Avatar>
        <Typography className={classes.name}>{name}</Typography>
        <Typography className={classes.amount}>
          <Money value={amount} />
        </Typography>
      </AccordionSummary>
      <BucketActions />
    </Accordion>
  );
};

type AvailableBucketActions = 'add' | 'withdraw' | 'default';

const BucketActions: FC = () => {
  const [action, setAction] = useState<AvailableBucketActions>('default');

  const setDefaultAction = () => setAction('default');

  switch (action) {
    case 'add':
      return <AddToBucket onCancel={setDefaultAction} onAdd={() => setDefaultAction()} />; //todo: zapis kwoty
    case 'withdraw':
      return <WithdrawFromBucket onCancel={setDefaultAction} onWithdraw={() => setDefaultAction()} />; //todo: zapis kwoty
    default:
      return <BucketActionButtons onChoice={setAction} />;
  }
};

interface BucketActionButtonsProps {
  onChoice: (action: AvailableBucketActions) => void;
}

const BucketActionButtons: FC<BucketActionButtonsProps> = ({ onChoice }) => {
  return (
    <>
      <Divider />
      <AccordionActions>
        <Button size="small" color="secondary" onClick={() => onChoice('withdraw')}>
          <RemoveCircleOutline /> Wypłać
        </Button>
        <Button size="small" color="primary" onClick={() => onChoice('add')}>
          <AddCircleOutline /> Dodaj
        </Button>
      </AccordionActions>
    </>
  );
};

interface AddToBucketProps {
  onAdd: (amount: number) => void;
  onCancel: () => void;
}

const AddToBucket: FC<AddToBucketProps> = ({ onAdd, onCancel }) => {
  const [error, setError] = useState(false);
  const [amount, setAmount] = useState('');

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value;
    setError(+value < 0);
    setAmount(value);
  };

  return (
    <>
      <AccordionDetails>
        <TextField
          required
          error={error}
          label="Ile?"
          id="add_amount"
          type="number"
          value={amount}
          onChange={handleChange}
          InputProps={{
            endAdornment: <InputAdornment position="end">zł</InputAdornment>,
          }}
        />
      </AccordionDetails>
      <Divider />
      <AccordionActions>
        <Button size="small" color="secondary" onClick={() => onAdd(+amount)}>
          <CancelOutlined /> Anuluj
        </Button>
        <Button size="small" color="primary" disabled={amount === '' || error} onClick={() => onCancel()}>
          <CheckCircleOutlined /> Zatwierdź
        </Button>
      </AccordionActions>
    </>
  );
};

interface WithdrawFromBucketProps {
  onWithdraw: (amount: number) => void;
  onCancel: () => void;
}

const WithdrawFromBucket: FC<WithdrawFromBucketProps> = ({ onWithdraw, onCancel }) => {
  const [error, setError] = useState(false);
  const [amount, setAmount] = useState('');
  const [reason, setReson] = useState('');

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value;
    setError(+value < 0);
    setAmount(value);
  };

  const handleChangeReason = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value;
    setReson(value);
  };

  return (
    <>
      <AccordionDetails>
        <TextField
          error={reason === ''}
          required
          label="Powód:"
          id="withdraw_reason"
          type="text"
          value={reason}
          onChange={handleChangeReason}
        />

        <TextField
          required
          error={error}
          label="Ile?"
          id="withdraw_amount"
          type="number"
          value={amount}
          onChange={handleChange}
          InputProps={{
            endAdornment: <InputAdornment position="end">zł</InputAdornment>,
          }}
        />
      </AccordionDetails>
      <Divider />
      <AccordionActions>
        <Button size="small" color="secondary" onClick={() => onWithdraw(+amount)}>
          <CancelOutlined /> Anuluj
        </Button>
        <Button
          size="small"
          color="primary"
          disabled={amount === '' || reason === '' || error}
          onClick={() => onCancel()}
        >
          <CheckCircleOutlined /> Zatwierdź
        </Button>
      </AccordionActions>
    </>
  );
};
